import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { QuestionsPage } from '../questions/questions';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';    // Imports various form things

/**
 * Generated class for the ReservePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reserve',
  templateUrl: 'reserve.html',
})
export class ReservePage {

  tncsForm: FormGroup; // Sets up comment object as a type of FormGroup (from reactive library)

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    // public viewCtrl: ViewController,    // This ensures the page can be imported as a modal
    private formBuilder: FormBuilder) {    // Injects FormBuilder into constructor
     
      this.tncsForm = this.formBuilder.group({
        Accept: [false, Validators.pattern('true')]
      });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ReservePage');
  }

  goToQuestions(event) {
    // That's right, we're pushing to ourselves!
    this.navCtrl.push(QuestionsPage);
    
  }

}



